body, #root{
  background-color: #282c34 !important;
  color: white;
}

footer {
  font-size: 10px;
  text-align: center;
}

.form {
  padding-bottom: 15px !important;
}

#nft-contents .card {
  border: none;
  margin: 10px 20px;
  background: transparent;
  cursor: pointer;
}

.rscard {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  /* background-color: rgb(245, 14, 14); */
  background-clip: border-box;
  border: 1 px solid rgba(0, 0, 0, .125);
  /* border-radius: 0.25 rem; */
  max-width: 250px;
}

.rscardNoSelected div {
  opacity: 0.5;
  transition: .1s;
}

/* .rscardSelected img{
  outline: 10px dashed green;
  outline-offset: -8px;
  transition: .1s;
} */
.rscardSelected div{
  opacity: 1;
  transition: .1s;
  /* border: 4px solid green;
  border-radius: 1.6rem; */
}

#nft-contents .rscard .card-img {
  /* height: 250ox;
  width: 155px; */
  transition: all 0.5s ease;
}

.card-img,
.card-img-top {
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
}

.card_lesson_list{
  display: 'flex';
  flex-wrap: 'wrap';
}

.row {
  margin-top: 5px !important;
  margin-bottom: 5px !important;
}

span.cant-cartas {
	position: absolute;
	width: 32px;
	height: 32px;
	/* vertical-align: middle; */
	text-align: center;
	border-radius: 50%;
	border: 0px solid black;
	/* font-size: inherit; */
	background-color: orange;
	top: -5%;
	/* left: 50%; */
	margin: 0;
	right: 0%;
	padding: 0;
}

/* xs */
.rscard {
  width: 30% !important;
}

/* sm */
@media (min-width: 768px) {
  .rscard {
    width: 13% !important;
  }
}

/* md */
@media (min-width: 992px) {
  .rscard {
    width: 13% !important;
  }
}

/* lg */
@media (min-width: 1200px) {
  .rscard {
    width: 13% !important;
  }
}

.price-data{
  padding-left: 10px;
}